import './styles.css';

const carData = [
	{ id: 1, make: 'Toyota', model: 'Camry', year: 2022, price: 25000 },
	{ id: 2, make: 'Honda', model: 'Civic', year: 2022, price: 22000 },
	{ id: 3, make: 'Ford', model: 'F-150', year: 2022, price: 35000 },
	{ id: 4, make: 'Chevrolet', model: 'Malibu', year: 2022, price: 27000 },
	{ id: 5, make: 'Volkswagen', model: 'Jetta', year: 2022, price: 24000 },
	{ id: 6, make: 'Hyundai', model: 'Elantra', year: 2022, price: 23000 },
	{ id: 7, make: 'Nissan', model: 'Altima', year: 2022, price: 26000 },
	{ id: 8, make: 'Kia', model: 'Optima', year: 2022, price: 25000 }
];
const DummyTable = () => {
	return (
		<table className='table'>
			<thead>
				<tr>
					<th>ID</th>
					<th>Make</th>
					<th>Model</th>
					<th>Year</th>
					<th>Price</th>
				</tr>
			</thead>
			<tbody>
				{carData.map(car => (
					<tr key={car.id}>
						<td>{car.id}</td>
						<td>{car.make}</td>
						<td>{car.model}</td>
						<td>{car.year}</td>
						<td>${car.price.toLocaleString()}</td>
					</tr>
				))}
			</tbody>
		</table>
	);
};
export default DummyTable;
